.brand-wrapper {
  padding: 20px 0px;
  max-width: 250px;
}

.brand-wrapper a {
  float: none;
  text-align: center;
  display: inline-block;
}

@media (min-width: 992px) {
  .brand-wrapper a {
    width: 100%;
  }
}

.brand-wrapper a img {
  display: inline;
}

@media (min-width: 992px) {
  .brand-wrapper a img {
    display: block;
  }
}

.navbar-brand {
  float: none;
  text-align: center;
  width: 100%;
}

@media (min-width: 992px) {
  .navbar-brand {
    width: 300px;
    float: left;
    padding: 41px 7px;
  }
}

.navbar-brand > img {
  display: inline-block;
}

.main-nav {
  margin-left: 300px;
}

.main-nav li.dropdown:hover ul.dropdown-menu {
  display: block;
}

.main-nav > .nav-item > .nav-link {
  padding: 2.5rem 1rem;
  margin-right: 20px;
  position: relative;
  color: #333333;
}

.navbar-nav .nav-item.active .nav-link, .navbar-nav .nav-item:hover .nav-link {
  background-color: transparent;
  color: #2780E3;
}

.dropdown-toggle:after {
  display: inline-block;
  width: 0;
  height: 0;
  position: absolute;
  margin-left: 15px;
  vertical-align: middle;
  top: 36px;
  right: 0px;
  font-family: FontAwesome;
  content: "\f107";
  border-top: 0;
  border-right: 0;
  border-left: 0;
}

.control-user.mobile a {
  display: block;
  width: 50%;
  float: left;
}

.search-desktop {
  max-width: 300px;
  margin: 33.5px 0px;
}

.search-desktop button.search-trigger.btn {
  background: transparent;
  color: #8B8B8B;
  font-size: 20px;
  padding: 0px;
}

.search-desktop button.search-trigger.btn:focus {
  outline: 0px;
}

.search-desktop .input-group-btn span.desktop-search-trigger {
  position: absolute;
  width: 44px;
  height: 42px;
  z-index: 3 !important;
  padding: 12px;
}

nav.navbar ul.nav li.dropdown:hover > .dropdown-menu {
  display: block;
  margin-top: 0px;
}

nav.navbar ul.nav li.dropdown:hover > .dropdown-menu ul > li > ul {
  margin-top: -5px;
  position: static !important;
  border: none !important;
  padding: 0 0 5px 0 !important;
}

nav.navbar ul.nav li.dropdown:hover > .dropdown-menu ul > li > ul > li a {
  padding: 0 0 2px 20px !important;
}

#searchBox .search-box-inner {
  padding: 10px 0px;
}

input.search-input, button.search-submit {
  min-height: 42px;
}

.autocomplete-suggestions {
  z-index: 2000 !important;
}

@media (min-width: 544px) {
  .autocomplete-suggestions {
    width: 526px !important;
  }
}

.navbar-nav .nav-item.open.yamm-fw {
  position: static;
}

.dropdown .sub-menu {
  display: none;
}

.dropdown .dropdown:hover > sub-menu {
  display: block;
}

ul.dropdown-menu.sub-menu {
  top: 0;
  left: 100%;
  margin-top: -1px;
}

.yamm-content a {
  color: #333333;
  font-weight: 300;
  line-height: 24px;
}

.yamm-content a:hover {
  background: transparent;
  text-decoration: underline;
}

.yamm-content a.h2 {
  color: #2780E3;
  margin: 30px 0;
  display: block;
}

.dropdown.yamm-fw {
  position: static;
}

.yamm-content {
  padding: 20px 30px;
  position: relative;
}

.yamm-content a {
  color: #333333;
  font-weight: 300;
  line-height: 24px;
}

.yamm-content a.h2 {
  color: #2780E3;
}

.dropdown.yamm-fw .dropdown-menu {
  left: 0;
  right: 0;
  margin: 0;
  border: 0;
  border-top: 1px solid #ECECEA;
  border: 1px solid #ECECEA;
}

ul.dropdown-menu {
  width: 100%;
  margin: 1px 0;
  background: #FFF;
  /* border: 1px solid #ECECEC; */
  box-shadow: none;
  border: 1px solid #ECECEA;
  min-width: 148px;
}

ul.dropdown-menu.yamm {
  left: auto;
}

.dropdown.open > a:first-child {
  color: #2780E3;
}

.main-nav > li > a {
  font-size: 16px;
  padding-left: 35px;
}

.navbar-icons > li {
  display: inline-block;
}

.navbar-icons i {
  color: #8B8B8B;
  font-size: 20px;
}

.mobile-nav {
  text-align: center;
  background-color: transparent;
  padding: 15px 0;
  border-top: 1px solid #ECECEA;
}

.mobile-nav.login-row .col-xs-3 {
  width: 25%;
}

.mobile-nav.login-row .col-xs-3:nth-of-type(2) {
  width: 25%;
}

.mobile-nav.login-row #login-change {
  vertical-align: middle;
  display: inline-block;
}

.mobile-nav.login-row #login-change div:nth-of-type(2) {
  display: none;
}

.mobile-nav .col-xs-3 {
  width: 20%;
}

.mobile-nav .col-xs-3:nth-of-type(2) {
  width: 40%;
}

.mobile-nav .toggle-basket-preview .hidden-sm-down {
  display: none;
}

.navbar-default {
  margin-bottom: 0;
  border-bottom: 1px solid #ECECEA;
}

.navbar-header {
  float: none;
}

@media (min-width: 992px) {
  .navbar-header {
    float: left;
  }
}

.navbar {
  padding: 0px;
}

#login-modal-wrapper button .dropdown-menu {
  box-shadow: none;
  min-width: 150px;
}

ul.nav li.dropdown:hover ul.dropdown-menu {
  display: block;
}

ul.nav.navbar-nav.navbar-right.mobile-icons {
  margin: 41px 7px;
}

ul.nav.navbar-nav.navbar-right.mobile-icons li {
  padding-left: 20px;
  display: inline-block;
}

a.dropdown-item.no-mega {
  padding: 8px 20px !important;
}

.top-header #login-change .account-menu.dropdown-menu {
  z-index: 1051;
  color: #000;
  padding: 0px;
  background: #FFF;
  box-shadow: none;
  border: 0px;
}

.top-header #login-change .account-menu.dropdown-menu a {
  color: #333333;
}

.top-header #login-change .account-menu.dropdown-menu a:hover {
  background-color: #FAF9F9;
}

.account-menu.dropdown-menu {
  border: 0;
  padding: 0;
  background: transparent;
}

#login-change .dropdown-toggle:after {
  content: "";
}

@media (max-width: 991px) {
  #login-change .list-group {
    padding-left: 0;
    margin-bottom: 0;
  }
  #login-change .list-group a {
    margin-right: 0;
    float: none;
    width: 100%;
    font-size: 14px;
    color: #333333;
    display: inline-block;
    vertical-align: baseline;
    text-align: center;
  }
}

@media (max-width: 991px) and (max-width: 767px) {
  #login-change .list-group a {
    font-size: 0;
    color: transparent;
  }
}

@media (max-width: 991px) and (min-width: 544px) {
  #login-change .list-group a i {
    margin-right: 10px;
  }
}

.myaccount .dropdown-toggle:after, .login .dropdown-toggle:after, .checkout .dropdown-toggle:after {
  content: "";
}

.dropdown-item:focus {
  background-color: transparent;
  outline: none;
}

.slider-content {
  color: #fff;
  position: absolute;
  bottom: 35px;
  padding: 0 20px;
  left: -14px;
}

@media (min-width: 544px) {
  .slider-content {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
}

@media (min-width: 992px) {
  .slider-content {
    width: 100%;
  }
}

.slider-content .btn-primary {
  width: 238px;
  margin-top: 20px;
}

.silder-img {
  height: 550px;
  background-size: cover;
  background-position: right;
  background-repeat: no-repeat;
  margin-left: -1px;
}

@media (min-width: 544px) {
  .silder-img {
    height: 500px;
  }
}

@media (min-width: 768px) {
  .silder-img {
    height: 600px;
  }
}

@media (min-width: 992px) {
  .silder-img {
    height: 700px;
  }
}

.highlights img {
  -webkit-backface-visibility: hidden;
  -o-transition: opacity .5s ease-in;
  transition: opacity .5s ease-in;
}

.highlights img:hover {
  opacity: .7;
}

.product-slider {
  padding: 40px 0;
}

@media (min-width: 544px) {
  .product-slider {
    padding: 50px 0;
  }
}

@media (min-width: 768px) {
  .product-slider {
    padding: 70px 0;
  }
}

.product-slider .product-box img {
  margin: 0 auto;
}

.product-slider .product-box h4 {
  margin-top: 15px;
  font-size: 18px;
}

.product-slider .product-box .price {
  color: #2780E3;
  font-size: 18px;
}

